const theme = createTheme({
  typography: {
    fontFamily: "Nunito",
  },
  palette: {
    primary: {
      main: "#7A0BC0",
    },
  },
});

export class Sckills extends React.Component {
  state = {
    len: [],
    front: [],
    other: [],
    ver: false,
  };

  componentDidMount = () => {
    this.setState({ len: lenguajes, front: fr, other: otr });
  };

  llamar() {
    if (this.state.ver) {
      this.setState({ ver: false });
    } else this.setState({ ver: true });
  }

  render() {
    return (
      <Grid container justifyContent="center">
        <ThemeProvider theme={theme}>
          <Box
            sx={{
              width: "100%",
            }}
          >
            <Grid container justifyContent="center" spacing={2}>
              <Grid item>
                <Typography variant="h2" className="texto">
                  Knowlege and
                </Typography>
              </Grid>
              <Grid item>
                <Typography variant="h2" className="texto">
                  Skills
                </Typography>
              </Grid>
            </Grid>
          </Box>
          <Box
            sx={{
              width: "100%",
              textAlign: "center",
              marginTop: "2%",
            }}
          >
            <Typography variant="h4" color={"white"} fontFamily="Segoe UI">
              BACKEND
            </Typography>
          </Box>
          <Grid container justifyContent="space-evenly" sx={{ width: "80%" }}>
            {this.state.len.map((value) => (
              <Sckill key={value.id} data={value}></Sckill>
            ))}
          </Grid>
          <Box
            sx={{
              width: "100%",
              textAlign: "center",
              marginTop: "2%",
            }}
          >
            <Typography variant="h4" color={"white"} fontFamily="Segoe UI">
              FRONTEND
            </Typography>
          </Box>
          <Grid container justifyContent="space-evenly" sx={{ width: "80%" }}>
            {this.state.front.map((value) => (
              <Sckill key={value.id} data={value}></Sckill>
            ))}
          </Grid>
          <Box
            sx={{
              width: "100%",
              textAlign: "center",
              marginTop: "2%",
            }}
          >
            <Typography variant="h4" color={"white"} fontFamily="Segoe UI">
              OTHER HABILITIES
            </Typography>
          </Box>
          <Grid container justifyContent="space-evenly" sx={{ width: "80%" }}>
            {this.state.other.map((value) => (
              <Sckill key={value.id} data={value}></Sckill>
            ))}
          </Grid>
        </ThemeProvider>
      </Grid>
    );
  }
}
